import React, { useState } from "react";
import Jumbotron from "react-bootstrap/Jumbotron";
// import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Navbar from "react-bootstrap/Navbar";
// import Nav from "react-bootstrap/Nav";
// import Form from "react-bootstrap/Form";
// import FormControl from "react-bootstrap/FormControl";
// import items from "./api/items.json";
// import Client from "./api/Client.js";
import Moment from "moment";
import _ from "lodash";

import "./App.css";
// import "./App.scss";
import PathCmdToBash from "./PathCmdToBash";
import HighlightFootball from "./HighlightFootball";

const ClientApi = {
    retrieveComicsByPageNames: function (comicItems) {
        return comicItems.reduce((acc, curr, currInd, arr) => {
            if (acc.length === 0) acc.push(curr);
            else {
                if (
                    !acc
                        .map((a) => a.base_site_url)
                        .includes(curr.base_site_url)
                )
                    acc.push(curr);
            }
            return acc;
        }, []);
    },
};

const ComicChapters = (props) => {
    const [comics, setComics] = useState(props.comics || []);
    const [siteName, setSiteName] = useState("All");

    const sortedComics = _.cloneDeep(comics);

    sortedComics.sort((a, b) => new Date(b.raw_time) - new Date(a.raw_time));

    const selectSiteNameHandler = (siteName) => {
        switch (siteName) {
            case "All":
                setSiteName(siteName);
                setComics(props.comics);
                break;
            default:
                setSiteName(siteName);
                setComics(
                    props.comics.filter((c) => c.base_site_name === siteName)
                );
        }
    };
    const iframe = `<iframe width='640' height='400' src='https://yfl.koralast.com/embed/KyXsaXYhuEkr4?autoplay=1&htmlplayer=1' style='border:none;' allowfullscreen></iframe>`;
    return (
        <>
            <Row className="justify-content-md-start">
                <div className="container my-4 text-center">
                    <HighlightFootball iframe={iframe} />
                </div>
            </Row>
            <Row className="justify-content-md-start">
                <div className="container my-4 text-center">
                    <span style={{ fontSize: "1.5em", color: "#333" }}>
                        Display:
                    </span>
                    <Button
                        className={`mx-1 my-1 ${
                            siteName === "All" ? "active" : ""
                        }`}
                        onClick={(e) => {
                            e.preventDefault();
                            selectSiteNameHandler("All");
                        }}
                        variant="outline-primary"
                    >
                        All
                    </Button>
                    {ClientApi.retrieveComicsByPageNames(props.comics).map(
                        function (comic, ind) {
                            const siteNames = [
                                ...new Set(comics.map((c) => c.base_site_name)),
                            ];
                            return (
                                <Button
                                    className={`mx-1 ${
                                        siteNames.length === 1 &&
                                        siteNames.includes(comic.base_site_name)
                                            ? "active"
                                            : ""
                                    }`}
                                    key={ind}
                                    onClick={(e) => {
                                        e.preventDefault();
                                        selectSiteNameHandler(
                                            comic.base_site_name
                                        );
                                    }}
                                    variant="outline-primary"
                                    href={comic.base_site_url}
                                >
                                    {comic.base_site_name}
                                </Button>
                            );
                        }
                    )}
                </div>
            </Row>
            <Row className="justify-content-md-start">
                {sortedComics.map((comic, ind) => {
                    return (
                        <Col xs={12} sm={6} md={4} lg={2} key={ind}>
                            <Card
                                border="info"
                                className="text-center my-1"
                                style={{
                                    // width: "14rem",
                                    height: "470px",
                                    padding: "0px",
                                }}
                            >
                                <Card.Header overflow="hidden">
                                    <Card.Title>
                                        <Card.Link
                                            className="text-dark"
                                            href={comic.base_site_url}
                                        >
                                            {_.truncate(comic.base_site_name, {
                                                length: 15,
                                            })}
                                        </Card.Link>
                                    </Card.Title>
                                </Card.Header>
                                <div
                                    style={{
                                        width: "100%",
                                        height: "288px",
                                        overflow: "hidden",
                                    }}
                                >
                                    <Card.Img
                                        variant="top"
                                        src={comic.cover_img}
                                        style={{
                                            width: "100%",
                                            height: "auto",
                                            // maxHeight: "250px",
                                            marginLeft: "50%",
                                            transform: "translateX(-50%)",
                                        }}
                                    />
                                </div>

                                <Card.Body>
                                    <Card.Title>
                                        <Card.Link
                                            className="text-dark "
                                            href={comic.comic_url}
                                        >
                                            {_.truncate(comic.comic_name, {
                                                length: 35,
                                            })}
                                        </Card.Link>
                                    </Card.Title>
                                    <Card.Subtitle className="mb-2 text-muted">
                                        {Moment(comic.raw_time).fromNow()}
                                    </Card.Subtitle>
                                    <a
                                        className={"m-1 chapterNumberLink"}
                                        href={comic.chapter_number_link}
                                    >
                                        {comic.chapter_number_text}
                                    </a>

                                    {comic.prev_chap ? (
                                        <a
                                            className={"m-1 chapterNumberLink"}
                                            href={
                                                comic.prev_chap
                                                    .chapter_number_link
                                            }
                                        >
                                            {
                                                comic.prev_chap
                                                    .chapter_number_text
                                            }
                                        </a>
                                    ) : (
                                        ""
                                    )}
                                </Card.Body>
                            </Card>
                        </Col>
                    );
                })}
            </Row>
        </>
    );
};

class App extends React.Component {
    state = {
        itemsLoading: "LOADING",
        items: [],
        loadedHour: 0,
    };

    loadItems = () => {
        fetch(`${process.env.PUBLIC_URL}/items.json`)
            .then((r) => r.json())
            .then((items) => {
                this.setState({ items, itemsLoading: "FINISHED" });
            });
    };

    componentWillMount() {
        const d = new Date();
        const hours = d.getHours();
        this.setState({
            loadedHour: hours,
        });
        this.loadItems();
    }

    componentDidMount() {
        const ms = 1;
        const s = 1000 * ms;
        // const m = 60 * s;
        this.reloadItemsInterval = setInterval(() => {
            const d = new Date();
            const hours = d.getHours();
            const minutes = d.getMinutes();

            if (hours >= 6 && hours <= 24) {
                if (minutes > 3) {
                    if (this.state.loadedHour !== hours) {
                        this.setState({
                            loadedHour: hours,
                        });
                        this.loadItems();
                    }
                }
            }
        }, 1 * s);
    }

    componentWillUnmount() {
        clearInterval(this.reloadItemsInterval);
    }

    render() {
        const items = this.state.items;
        return (
            <div className="app">
                <div className="headerApp">
                    <Navbar bg="dark" variant="dark">
                        <Navbar.Brand href="#home">
                            <img
                                alt=""
                                src={process.env.PUBLIC_URL + "/logorb.svg"}
                                width="30"
                                height="30"
                                className="d-inline-block align-top"
                            />{" "}
                            Latest Comic Chapters
                        </Navbar.Brand>
                    </Navbar>
                </div>
                <div className="bodyApp">
                    {/* <div className="container my-4">
                        <PathCmdToBash />
                    </div> */}
                    <div className="p-4">
                        {
                            {
                                LOADING: <h1>"Loading..."</h1>,
                                FINISHED: <ComicChapters comics={items} />,
                            }[this.state.itemsLoading]
                        }
                    </div>
                </div>
                <div className="footerApp">
                    <Jumbotron>
                        <Row>
                            <Col
                                style={{
                                    borderRight: "1px dashed black",
                                }}
                            >
                                <h1>More Info: </h1>
                                <p>
                                    I get sources from other pages as mentioned
                                    on the top of every card. So please access
                                    their pages to read more comics.
                                </p>
                                <p>
                                    About @copyrights, please contact:{" "}
                                    <b>
                                        <i>Duongntse@gmail.com</i>
                                    </b>
                                </p>
                            </Col>
                            <Col>
                                <h1>Comic Sites:</h1>
                                {
                                    {
                                        LOADING: <h1>"Loading..."</h1>,
                                        FINISHED: ClientApi.retrieveComicsByPageNames(
                                            items
                                        ).map((comic, ind) => (
                                            <Button
                                                className={"mx-1"}
                                                key={ind}
                                                // target="_blank"
                                                variant="outline-primary"
                                                href={comic.base_site_url}
                                            >
                                                {comic.base_site_name}
                                            </Button>
                                        )),
                                    }[this.state.itemsLoading]
                                }
                            </Col>
                        </Row>
                    </Jumbotron>
                </div>
            </div>
        );
    }
}

export default App;
